import React, { Component } from 'react'
import { Div } from 'glamorous'
import TimeLine from '../TimeLine'
import Events from '../Events'

export default class TimeTable extends Component {
  innerStyles = {
    height: '100%',
    paddingBottom: '17px',
    overflowX: 'scroll',
    overflowY: 'hidden',
    backgroundColor: '#f6f7f9'
  }

  outerStyles = {
    overflow: 'hidden',
    '@media(max-width: 540px)': {
      width: '50%'
    }
  }

  handleScroll = () => {
    this.timeTable.addEventListener('wheel', e => {
      e.preventDefault()
      if(e.deltaY > 0) {
        this.timeTable.scrollLeft = this.timeTable.scrollLeft + 20
      }

      else {
        this.timeTable.scrollLeft = this.timeTable.scrollLeft - 20
      }
    })
  }

  componentDidMount () {
    this.handleScroll()
  }
  
  render() {
    return (
      <Div css={this.outerStyles}>
        <Div 
          css={this.innerStyles}
          innerRef={ref => this.timeTable = ref}
        >
          <TimeLine />
          <Events />
        </Div>
      </Div>
    )
  }
}


