import React, { Component } from 'react'
import { Div } from 'glamorous'
import DayPicker from 'react-day-picker'
import { connect } from 'react-redux'
import updateNewEventDate from '../action-creators/eventsReducer/updateDate'
import updateCurrentDate from '../action-creators/dateReducer/updateCurrentDate'
import updateBtnState from '../action-creators/dateReducer/updateBtnState'
import PropTypes from 'prop-types'

class Calendar extends Component {
  static propTypes = {
    date: PropTypes.number.isRequired,
    month: PropTypes.number.isRequired,
    updateNewEventDate: PropTypes.func.isRequired,
    updateCurrentDate: PropTypes.func.isRequired,
    updateBtnState: PropTypes.func.isRequired
  }

  containerStyles = {
    position: 'absolute',
    backgroundColor: '#ffffff',
    marginLeft: '8px',
    borderRadius: '5px',
    border: '1px solid #e9ecef',
    boxShadow: '1px 2px 26px 0px rgba(41, 41, 42, 0.1)',
    zIndex: '100',
    '@media(max-width: 540px)': {
      margin: '0px',
      left: '50%',
      transform: 'translateX(-50%)'
    }
  }

  now = new Date()

  modifiersStyles = {
    highlightedDate: {
      color: '#fff',
      backgroundColor: '#55a8fe',
      borderRadius: '50%'
    }
  }

  updateDate = day => {
    const { updateNewEventDate, updateCurrentDate, updateBtnState } = this.props
    const newDate = day.getDate()
    const newMonth = day.getMonth()

    updateNewEventDate(newMonth, newDate)
    updateCurrentDate(newMonth, newDate)
    updateBtnState()
  }

  render () {
    const { date, month, locale, months, daysShort, labels, firstDay } = this.props
    const modifiers = {
      highlightedDate: new Date(this.now.getFullYear(), month, date) 
    }

    return (
      <Div 
        css={this.containerStyles}
        innerRef={ref => this.calendar = ref}
      >
        <DayPicker 
          locale={locale}
          months={months}
          weekdaysShort={daysShort}
          firstDayOfWeek={firstDay}
          labels={labels}
          onDayClick={this.updateDate}
          modifiers={modifiers}
          modifiersStyles={this.modifiersStyles}
          disabledDays={[{
            before: this.now
          }]}
          fromMonth={this.now}
        />
      </Div>
    )
  }
}

const mapStateToProps = state => ({
  date: state.dateReducer.date,
  month: state.dateReducer.month,
  locale: state.dateReducer.locale,
  months: state.dateReducer.months,
  daysShort: state.dateReducer.daysShort,
  labels: state.dateReducer.labels,
  firstDay: state.dateReducer.firstDay
})

const mapDispatchToProps = {
  updateNewEventDate,
  updateCurrentDate,
  updateBtnState
}

export default connect(mapStateToProps, mapDispatchToProps)(Calendar)


